html, #app, body {
    height: 100%;
}

/* works not really fast,  but for one rule it's ok, i guess) */
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.content {
    height: 100%;
    background: #f0f0f0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.content .header {
    height: 10vh;
    -webkit-box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.18);
            box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.18);
    padding: 8px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    top: 0;
    z-index: 10;
    background: white;
}

.header__button-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.header__content {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}

.button {
    width: 48px;
    height: 36px;
    padding: 4px;
    cursor: pointer;
}

.button__line {
    background: #000000;
    width: 100%;
    height: 5px;
    border-radius: 10px;
    margin: 3px;
    -webkit-transition: .4s ease;
    -o-transition: .4s ease;
    transition: .4s ease;
    position: relative;
    opacity: 1;
}

.button__line.active {
    opacity: 0;
}

.button :nth-child(2).active {
    opacity: 1;
    background: transparent;
}

.button :nth-child(2):before,
.button :nth-child(2):after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    border-radius: 10px;
    -webkit-transition: .4s ease;
    -o-transition: .4s ease;
    transition: .4s ease;
}

.button :nth-child(2).active:before {
    -webkit-transform: rotateZ(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotateZ(-45deg);
}

.button :nth-child(2).active:after {
    -webkit-transform: rotateZ(45deg);
        -ms-transform: rotate(45deg);
            transform: rotateZ(45deg);
}

.content .main {
    -ms-flex: 6 1;
        flex: 6 1;
    position: relative;
    margin-top: 15vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.counter-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 8px;
    -webkit-box-shadow: 2px 2px 6px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 2px 2px 6px 0 rgba(0, 0, 0, 0.12);
}

.control {
    border: none;
    outline: none;
    margin: 5px;
    padding: 8px;
    cursor: pointer;
    color: white;
    border-radius: 3px;
    -webkit-box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.38);
            box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.38);
    -webkit-transition: .2s ease;
    -o-transition: .2s ease;
    transition: .2s ease;
}

.control:active {
    -webkit-box-shadow: 2px 2px 3px 2px rgba(0, 0, 0, 0.38);
            box-shadow: 2px 2px 3px 2px rgba(0, 0, 0, 0.38);
}

.control.stop {
    background: #ca0000;
}

.control.force {
    background: #008f00;
}

.sidebar {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 20vw;
    background: #ffffff;
    -webkit-transition: .4s ease;
    -o-transition: .4s ease;
    transition: .4s ease;
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
}

.sidebar.shown {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}

.content .footer {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: white;
    border-top: 1px solid rgba(128, 128, 128, 0.39);
}




